img {
  width: 100vw;
  overflow: hidden;
  margin-top: 55px;
  height: 65vh;
  max-height: 600px;
  background-color: #4ea699;
}

.styled-text-cont h1 {
  display: inline-flex;
  margin: 5vw 2vh;
  text-align: start;
  flex-direction: column;
  font-family: Lato;
  color: #f7f5f1;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.styled-text-cont h1 span {
  font-size: 1.2em;
  margin: 10px 0;
  color: #4ea699;
}